import React, { useEffect, useState } from 'react';
import bgcImage from '../Auth/images/loading.jpg';
import loadingImage from '../Auth/images/logoLoading.svg';
import styles from './Loading.module.scss';
import styled, { keyframes } from 'styled-components';

const Loader = styled.div`
	width: 890px;
	height: 21px;

	border: 1px solid #565656;
	border-radius: 12px;

	background-color: #333333;
	position: relative;
`;

const fill = keyframes`
  from {
    width: 0%;
  }

  to {
    width: 100%;
  }
`;

const Fill = styled.div`
	height: 100%;
	background-color: #ff992c;
	border-radius: 12px;
	position: absolute;
	animation: ${fill} 3s linear;
`;

export default () => {
	const [loaderCompon, setLoaderCompon] = useState(<div />);

	useEffect(() => {
		setLoaderCompon(
			<Loader>
				<Fill />
			</Loader>
		);
	}, []);

	return (
		<div className={styles.page} style={{ backgroundImage: `url(${bgcImage})` }}>
			<img className={styles.logo} src={loadingImage} alt="loading image" />
			<div className={styles.loaderPosition}>{loaderCompon}</div>
		</div>
	);
};
